import requestUtil from '@/utils/request'

// 领券中心-查询可领优惠券
export const getCouponListApi = () => {
  return requestUtil.get('/coupon/list')
}

// 领取优惠券
export const onReceiveCouponApi = (couponId) => {
  return requestUtil({
    method: 'post',
    url: '/myCoupon/receive',
    data: { couponId }
  })
}

// 我的优惠券列表
export const getMyCouponListApi = (dataType, page) => {
  return requestUtil({
    url: 'myCoupon/list',
    params: {
      dataType,
      page
    }
  })
}
